body,p,html,input,ul,li{margin:0;padding:0}
li{list-style:none;}

a,div,p{font-family:"微软雅黑"}
*{font-family:"微软雅黑"}
a{text-decoration:none}
#one{width:710px;height:248px;margin:0 20px;border-bottom:1px solid #c8c7cc;display:flex;align-items:center}
.one1{width:188px;height:188px;float:left;margin-right:23px;position:relative}
.photo1{background-image:url(../../static/photo/2.jpg)}
.photo2{background-image:url(../../static/photo/3.jpg)}
.photo3{background-image:url(../../static/photo/4.jpg)}
.photo4{background-image:url(../../static/photo/5.jpg)}
.photo5{background-image:url(../../static/photo/6.jpg)}
.one1_1{width:188px;line-height:36px;position:absolute;bottom:0;left:0;font-size:24px;color:#fff;text-align:center}
.rgba1{background-color:rgba(0,0,0,.7)}
.rgba2{background-color:rgba(0,0,0,.5)}
.rgba3{background-color:rgba(0,0,0,.6)}
.rgba4{background-color:rgba(0,0,0,.4)}
.rgba5{background-color:rgba(0,0,0,.3)}
.one2{width:500px;height:188px;position:relative;float:left}
.fonttwo1{font-size:28px;word-spacing:-2px;white-space:nowrap}
.fonttwo3{margin-left:-14px}
#fonttwo2{font-size:24px;color:#d0d0d0;word-spacing:30px;margin-top:10px}
.one3{width:30px;line-height:30px;float:left;text-align:center;border-radius:6px;margin-right:10px;margin-top:14px;color:#fff}
.beijing1{background-color:#ff9a14}
.beijing2{background-color:#75bd03}
#one4{width:128px;line-height:32px;position:absolute;bottom:0;right:0;color:#ff4e07;font-size:40px;text-align:bottom}
#fonttwo4{font-size:24px}
#top{width:750px;height:128px;background-color:#0ca3f0}
#top1{width:100%;height:88px;position:relative;top:40px;display:flex;align-items:center;justify-content:center;position:relative}
#top1 .icon-fanhui{font-size:33px;position:absolute;top:27.5px;left:20px;color:#fff}
#fonttop{font-size:36px;color:#fff}
#top1 .icon-yuanjiaojuxing1{font-size:30px;position:absolute;top:29px;right:20px;color:#fff}





